import React, { useState, useEffect } from "react";
import { makeStyles, withStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import {
  MenuList,
  MenuItem,
  Typography,
  TablePagination,
} from "@material-ui/core";

import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";

import Avatar from "@material-ui/core/Avatar";

import MuiAccordion from "@material-ui/core/Accordion";
import MuiAccordionSummary from "@material-ui/core/AccordionSummary";
import MuiAccordionDetails from "@material-ui/core/AccordionDetails";

import AddIcon from "@material-ui/icons/Add";
import RemoveIcon from "@material-ui/icons/Remove";

import Chip from "@material-ui/core/Chip";

import Moment from "react-moment";
import "moment-timezone";

import Statuses from "./statuses";
import Modal from "./modal";

import auth from "../../helpers/Auth";

import IncompleteIcon from "../../assets/images/incomplete.png";
import StoredIcon from "../../assets/images/stored.png";
import RedBox from "../../assets/images/red-box.png";
import PortDefaultImg from "../../assets/images/port-default.png";

import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.min.css";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  header: {
    paddingTop: "19px",
    paddingBottom: "25px",
    paddingLeft: "25px",
    fontWeight: "bold",
    borderBottom: "1px solid rgba(33, 37, 41, 0.16)",
    fontSize: "14px",
    paddingRight: "20px",
  },
  column: {
    "border-right": "1px solid rgba(33, 37, 41, 0.16) !important",
    height: "100vh",
    paddingRight: "0 !important",
  },
  small: {
    width: "16px",
    height: "16px",
    margin: "0 12px",
  },
  cell: {
    padding: "4px 16px",
  },
  tableRow: {
    "&:hover": {
      backgroundColor: "rgba(234, 33, 33, 0.08) !important",
      boxShadow: "inset 0px -1px 0px rgba(33, 37, 41, 0.16)",
    },
  },
  chip: {
    borderRadius: "4px",
  },
  port: {
    width: "24px",
    height: "16px",
    float: "left",
    marginRight: "10px",
  },
}));

const Accordion = withStyles({
  root: {
    border: "1px solid rgba(0, 0, 0, .125)",
    boxShadow: "none",
    "&:not(:last-child)": {
      borderBottom: 0,
    },
    "&:before": {
      display: "none",
    },
    "&$expanded": {
      margin: "0",
    },
    content: {
      "&$expanded": {
        fontWeight: "bold",
      },
    },
  },
  expanded: {},
})(MuiAccordion);

const AccordionSummary = withStyles({
  root: {
    backgroundColor: "rgba(33, 37, 41, 0.02)",
    borderBottom: "1px solid rgba(0, 0, 0, .125)",
    marginBottom: -1,
    boxShadow: "inset 0px -1px 0px rgba(33, 37, 41, 0.16)",
    maxHeight: "24px !important",
    minHeight: "24px !important",
  },
  content: {
    "&$expanded": {
      margin: "0",
      fontSize: "10px",
    },
  },
  expanded: {},
})(MuiAccordionSummary);

const AccordionDetails = withStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
  },
}))(MuiAccordionDetails);

// start of component
const Index = ({ history }) => {
  const classes = useStyles();

  const [values, setValues] = useState({
    importers: [],
    modes: [],
    ports: [],
    selected: "",
    rowsPerPage: 10,
    page: 0,
    total: 0,
    incompleteDeclarations: [],
    storedDeclarations: [],
    selectedImporterName: "",
    expanded: "panel1",
  });

  const {
    importers,
    selected,
    modes,
    ports,
    rowsPerPage,
    page,
    total,
    incompleteDeclarations,
    selectedImporterName,
    expanded,
    storedDeclarations,
  } = values;

  useEffect(() => {
    fetchData();
  }, [page, rowsPerPage, selected]);

  // function for changing rows per page
  const handleChangeRowsPerPage = (e) => {
    setValues({
      ...values,
      rowsPerPage: parseInt(e.target.value, 10),
      page: 0,
    });
  };

  // function for changing page
  const handleChangePage = (event, newPage) => {
    setValues({
      ...values,
      page: newPage,
    });
  };

  const fetchData = async () => {
    const userData = auth.currentUser();
    const email = userData.emails[0];
    const apiPage = page + 1;

    const respImporters = await axios({
      method: "GET",
      url: `${process.env.REACT_APP_API}GetUserImporters`,
      headers: {
        Authorization: "token",
        AccessEmail: email,
      },
    });

    const respModes = await axios({
      method: "GET",
      url: `${process.env.REACT_APP_API}GetDeclarationModels`,
      headers: {
        Authorization: "token",
        AccessEmail: email,
      },
    });

    const respPorts = await axios({
      method: "GET",
      url: `${process.env.REACT_APP_API}GetPorts`,
      headers: {
        Authorization: "token",
        AccessEmail: email,
      },
    });

    const respIncomplete = await axios({
      method: "GET",
      url: `${process.env.REACT_APP_API}GetDeclarations?page=${apiPage}&per_page=${rowsPerPage}&importerId=${selected}&status=10`,
      headers: {
        Authorization: "token",
        AccessEmail: email,
      },
    });

    const respStored = await axios({
      method: "GET",
      url: `${process.env.REACT_APP_API}GetDeclarations?page=${apiPage}&per_page=${rowsPerPage}&importerId=${selected}&status=6`,
      headers: {
        Authorization: "token",
        AccessEmail: email,
      },
    });

    setValues({
      ...values,
      importers: respImporters.data.data,
      modes: respModes.data.data,
      ports: respPorts.data.data,
      incompleteDeclarations: respIncomplete.data.data,
      storedDeclarations: respStored.data.data,
      // selected: respImporters.data.data[0].user.id,
      selectedImporterName:
        selected !== ""
          ? selectedImporterName
          : `${respImporters.data.data[0].user.first_name} ${respImporters.data.data[0].user.last_name}`,
    });
  };

  const handleSelect = (id, name) => {
    // window.alert(name);
    setValues({ ...values, selected: id, selectedImporterName: name });
  };

  const handleChange = (name) => (event) => {
    setValues({ ...values, [name]: event.target.value });
  };

  return (
    <div className={classes.root}>
      <Grid container spacing={3}>
        <Grid className={classes.column} item xs={12} sm={3}>
          <div>
            <div className={classes.header}>Importers</div>
            <MenuList>
              {importers.map((value, key) => (
                <MenuItem
                  className="menu-links"
                  onClick={() =>
                    handleSelect(
                      value.user.id,
                      `${value.user.first_name} ${value.user.last_name}`
                    )
                  }
                  selected={
                    (selected === "" && key === 0) || selected === value.user.id
                      ? true
                      : false
                  }
                  key={key}
                >
                  {value.user.first_name} {value.user.last_name}
                </MenuItem>
              ))}
            </MenuList>
          </div>
        </Grid>
        <Grid
          className={classes.column}
          item
          xs={12}
          sm={9}
          style={{ paddingLeft: "0" }}
        >
          <div className={classes.header}>
            Declarations{" "}
            <span className="ereg-tag">{selectedImporterName}</span>
            <span style={{ float: "right" }}>
              <Modal
                selectedImporter={selected}
                importersList={importers}
                modes={modes}
                ports={ports}
                fetch={fetchData}
              />
            </span>
          </div>

          <TableContainer component={Paper}>
            <ToastContainer />
            <Table className="tble" aria-label="simple table">
              <TableHead>
                <TableRow>
                  <TableCell className="lodge-tbl-head">Status</TableCell>
                  <TableCell className="lodge-tbl-head">SC</TableCell>
                  <TableCell className="lodge-tbl-head">Port</TableCell>
                  <TableCell className="lodge-tbl-head">B/L No.</TableCell>
                  <TableCell className="lodge-tbl-head">Entry No.</TableCell>
                  <TableCell className="lodge-tbl-head">Year</TableCell>
                  <TableCell className="lodge-tbl-head">Date</TableCell>
                  <TableCell className="lodge-tbl-head">Time</TableCell>
                  <TableCell className="lodge-tbl-head">Importer</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                <TableRow key={2}>
                  <TableCell component="th" colSpan={9} padding="none">
                    <Accordion
                      key={1}
                      square
                      onChange={handleChange(`panel1`)}
                      expanded={true}
                    >
                      <AccordionSummary
                        expandIcon={
                          expanded === `panel1` ? <RemoveIcon /> : <AddIcon />
                        }
                        aria-controls={`panel1d-content`}
                        id={`panel1d-header`}
                      >
                        <strong>{storedDeclarations.length} Stored</strong>
                      </AccordionSummary>
                    </Accordion>
                  </TableCell>
                </TableRow>
                {storedDeclarations.map((row, key) => (
                  <TableRow
                    hover={true}
                    className={classes.tableRow}
                    key={row.id}
                  >
                    <TableCell className={classes.cell} component="th">
                      <Avatar
                        alt="Stored"
                        src={StoredIcon}
                        className={classes.small}
                      />
                    </TableCell>
                    <TableCell className={classes.cell} component="th">
                      <Avatar
                        alt="Red"
                        src={RedBox}
                        className={classes.small}
                        variant="square"
                      />
                    </TableCell>
                    <TableCell className={classes.cell} component="th">
                      <Avatar
                        alt="Port"
                        src={PortDefaultImg}
                        className={classes.port}
                        variant="square"
                      />
                      {row.port.code}
                    </TableCell>
                    <TableCell className={classes.cell} component="th">
                      <strong>{row.bill_of_lading.bl_number}</strong>
                    </TableCell>
                    <TableCell className={classes.cell} component="th">
                      {row.id}
                    </TableCell>
                    <TableCell className={classes.cell} component="th">
                      <Chip
                        className={classes.chip}
                        variant="outlined"
                        label={row.year}
                      />
                    </TableCell>
                    <TableCell className={classes.cell} component="th">
                      {row.date}
                    </TableCell>
                    <TableCell className={classes.cell} component="th">
                      {row.time}
                    </TableCell>
                    <TableCell className={classes.cell} component="th">
                      <Avatar>
                        {row.user.first_name.charAt(0)}
                        {row.user.last_name.charAt(0)}
                      </Avatar>
                    </TableCell>
                  </TableRow>
                ))}
                <TableRow key={1}>
                  <TableCell component="th" colSpan={9} padding="none">
                    <Accordion
                      key={2}
                      square
                      onChange={handleChange(`panel1`)}
                      expanded={true}
                    >
                      <AccordionSummary
                        expandIcon={
                          expanded === `panel1` ? <RemoveIcon /> : <AddIcon />
                        }
                        aria-controls={`panel1d-content`}
                        id={`panel1d-header`}
                      >
                        <strong>
                          {incompleteDeclarations.length} Incomplete
                        </strong>
                      </AccordionSummary>
                    </Accordion>
                  </TableCell>
                </TableRow>
                {incompleteDeclarations.map((row, key) => (
                  <TableRow
                    onClick={() => {
                      history.push(`declarations/${row.id}`);
                    }}
                    key={row.id}
                    hover={true}
                    className={classes.tableRow}
                  >
                    <TableCell className={classes.cell} component="th">
                      <Avatar
                        alt="Incomplete"
                        src={IncompleteIcon}
                        className={classes.small}
                      />
                    </TableCell>
                    <TableCell className={classes.cell} component="th">
                      <Avatar
                        alt="Red"
                        src={RedBox}
                        className={classes.small}
                        variant="square"
                      />
                    </TableCell>
                    <TableCell className={classes.cell} component="th">
                      <Avatar
                        alt="Port"
                        src={PortDefaultImg}
                        className={classes.port}
                        variant="square"
                      />
                      {row.port.code}
                    </TableCell>
                    <TableCell className={classes.cell} component="th">
                      <strong>{row.bill_of_lading.bl_number}</strong>
                    </TableCell>
                    <TableCell className={classes.cell} component="th">
                      {row.id}
                    </TableCell>
                    <TableCell className={classes.cell} component="th">
                      <Chip
                        className={classes.chip}
                        variant="outlined"
                        label={row.year}
                      />
                    </TableCell>
                    <TableCell className={classes.cell} component="th">
                      {row.date}
                    </TableCell>
                    <TableCell className={classes.cell} component="th">
                      {row.time}
                    </TableCell>
                    <TableCell className={classes.cell} component="th">
                      <Avatar>
                        {row.user.first_name.charAt(0)}
                        {row.user.last_name.charAt(0)}
                      </Avatar>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </Grid>
      </Grid>
    </div>
  );
};

export default Index;
