const Loading = <CircularProgress size={14} color="#FFFFFF" />;

export default function TransitionsModal({ showBox }) {
  const classes = useStyles();

  useEffect(() => {
    getBls();
  }, []);

  const [values, setValues] = useState({
    isConfigOpen: false,
    showBoxes: true,
  });

  const { isConfigOpen, showBoxes } = values;

  const handleConfigOpen = () => {
    setValues({ ...values, isConfigOpen: true });
  };

  const handleShowBox = () => {
    showBox(!showBoxes);
    setValues({ ...values, showBoxes: !showBoxes });
  };

  const handleConfigClose = () => {
    setValues({
      ...values,
      isConfigOpen: false,
    });
  };

  const handleChange = (name) => (event) => {
    setValues({ ...values, [name]: event.target.value });
  };

  const getBls = async () => {
    const userData = auth.currentUser();
    const email = userData.emails[0];

    const respBls = await axios({
      method: "GET",
      url: `${process.env.REACT_APP_API}GetBillOfLadings?per_page=1000`,
      headers: {
        Authorization: "token",
        AccessEmail: email,
      },
    });

    setValues({ ...values, blList: respBls.data.data });
  };

  return (
    <div>
      <Button
        onClick={handleConfigOpen}
        style={{
          border: "1px solid #212529",
          boxSizing: "border-box",
          borderRadius: "4px",
          marginLeft: "10px",
          height: "100%",
        }}
      >
        ...
      </Button>
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={isConfigOpen}
        width={1}
        onClose={handleConfigClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={isConfigOpen}>
          <div className={classes.paper}>
            <div className={classes.header}>
              <span>Show Box Numbers</span>
              <span className={classes.close}>
                <FormControlLabel
                  control={
                    <Switch
                      size="small"
                      onChange={handleShowBox}
                      checked={showBoxes}
                    />
                  }
                />
              </span>
            </div>
            <div className={classes.body}>
              <a
                className="danger-text"
                onClick={() => {
                  window.location.reload();
                }}
              >
                Reset Form
              </a>
            </div>
          </div>
        </Fade>
      </Modal>
    </div>
  );
}
