import React, { useState, useEffect } from "react";

import { withStyles } from "@material-ui/core/styles";
import MuiAccordion from "@material-ui/core/Accordion";
import MuiAccordionSummary from "@material-ui/core/AccordionSummary";
import MuiAccordionDetails from "@material-ui/core/AccordionDetails";
import Typography from "@material-ui/core/Typography";
import AddIcon from "@material-ui/icons/Add";
import RemoveIcon from "@material-ui/icons/Remove";

import auth from "../../helpers/Auth";

import axios from "axios";

const Accordion = withStyles({
  root: {
    border: "1px solid rgba(0, 0, 0, .125)",
    boxShadow: "none",
    "&:not(:last-child)": {
      borderBottom: 0,
    },
    "&:before": {
      display: "none",
    },
    "&$expanded": {
      margin: "auto",
    },
  },
  expanded: {},
})(MuiAccordion);

const AccordionSummary = withStyles({
  root: {
    backgroundColor: "rgba(0, 0, 0, .03)",
    borderBottom: "1px solid rgba(0, 0, 0, .125)",
    marginBottom: -1,
  },
  content: {
    "&$expanded": {
      margin: "12px 0",
    },
  },
  expanded: {},
})(MuiAccordionSummary);

const AccordionDetails = withStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
  },
}))(MuiAccordionDetails);

const Statuses = () => {
  const [values, setValues] = useState({
    expanded: "",
    statuses: [],
  });

  const { expanded, statuses } = values;

  const handleChange = (panel) => (event, newExpanded) => {
    setValues({
      ...values,
      expanded: newExpanded ? panel : false,
    });
  };

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    const userData = auth.currentUser();
    const email = userData.emails[0];

    const respStatuses = await axios({
      method: "GET",
      url: `${process.env.REACT_APP_API}GetDeclarationStatus`,
      headers: {
        Authorization: "token",
        AccessEmail: email,
      },
    });

    setValues({
      ...values,
      statuses: respStatuses.data.data,
    });
  };

  return (
    <div>
      {statuses.map((value, key) => (
        <Accordion key={key} square onChange={handleChange(`panel${key}`)}>
          <AccordionSummary
            expandIcon={
              expanded === `panel${key}` ? <RemoveIcon /> : <AddIcon />
            }
            aria-controls={`panel${key}d-content`}
            id={`panel${key}d-header`}
          >
            <Typography>{value.name}</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography>
              
            </Typography>
          </AccordionDetails>
        </Accordion>
      ))}
    </div>
  );
};

export default Statuses;
