import React, { useState, useEffect } from "react";

// load child components
import Table from "./table";
import Modal from "./modal";

import auth from "../../helpers/Auth";
import axios from "axios";

const NotificationsManagement = ({ match, history }) => {
  const [values, setValues] = useState({
    list: [],
    users: [],
  });

  const { list, users } = values;

  useEffect(() => {
    const fetchData = async () => {
      const userData = auth.currentUser();
      const email = userData.emails[0];

      const respNotifs = await axios({
        method: "GET",
        url: `${process.env.REACT_APP_API}GetNotifications?per_page=1000`,
        headers: {
          Authorization: "token",
          AccessEmail: email,
        },
      });

      const respUsers = await axios({
        method: "GET",
        url: `${process.env.REACT_APP_API}GetUsers?per_page=1000`,
        headers: {
          Authorization: "token",
          AccessEmail: email,
        },
      });

      setValues({
        ...values,
        list: respNotifs.data.data,
        users: respUsers.data.data,
      });
    };

    fetchData();
  }, []);

  return (
    <div className="component-group-wrapper" style={{ padding: "20px" }}>
      <Modal users={users} />
      <Table list={list} />
    </div>
  );
};

export default NotificationsManagement;
