import React, { useState, useEffect } from "react";

import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import { TablePagination } from "@material-ui/core";

import auth from "../../helpers/Auth";

import Modal from "./modal";

import axios from "axios";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.min.css";

const Index = () => {
  const [values, setValues] = useState({
    list: [],
    rowsPerPage: 10,
    page: 0,
    total: 0,
  });

  const { list, rowsPerPage, page, total } = values;

  useEffect(() => {
    fetchData();
  }, [page, rowsPerPage]);

  // function for get roles api call
  const fetchData = async () => {
    const userData = auth.currentUser();
    const email = userData.emails[0];
    const apiPage = page + 1;

    const respRoles = await axios({
      method: "GET",
      url: `${process.env.REACT_APP_API}GetRoles?page=${apiPage}&per_page=${rowsPerPage}`,
      headers: {
        Authorization: "token",
        AccessEmail: email,
      },
    });

    setValues({
      ...values,
      list: respRoles.data.data,
      total: respRoles.data.total,
    });
  };

  // function for changing rows per page
  const handleChangeRowsPerPage = (e) => {
    setValues({
      ...values,
      rowsPerPage: parseInt(e.target.value, 10),
      page: 0,
    });
  };

  // function for changing page
  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const setPage = (page) => {
    setValues({
      ...values,
      page: page,
    });
  };

  return (
    <TableContainer component={Paper} style={{ padding: "20px" }}>
      <Modal fetch={fetchData} set={setPage} />
      <ToastContainer />
      <Table className="tble" aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>Name</TableCell>
            <TableCell>Description</TableCell>
            <TableCell></TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {list.map((row) => (
            <TableRow key={row.id}>
              <TableCell component="th" scope="row">
                {row.name}
              </TableCell>
              <TableCell component="th" scope="row">
                {row.description}
              </TableCell>
              <TableCell component="th" scope="row"></TableCell>
            </TableRow>
          ))}
        </TableBody>
        <TablePagination
          count={total}
          page={page}
          onChangePage={handleChangePage}
          rowsPerPage={rowsPerPage}
          onChangeRowsPerPage={handleChangeRowsPerPage}
        />
      </Table>
    </TableContainer>
  );
};

export default Index;
