import { removeCommaCurrency } from "../helpers/Dev";

import auth from "../helpers/Auth";

import axios from "axios";

/** ITEMS START **/

export const getItemDutiableValue = (
  customsValue,
  itemFreight,
  itemInsurance,
  itemOthers
) => {
  // add all params
  return customsValue + itemFreight + itemInsurance + itemOthers;
};

export const getProRateValue = (customsValue, totalCustomsValue) => {
  // add all params
  return customsValue / totalCustomsValue;
};

export const getCustomsDuty = (rate, dutiableValue) => {
  // add all params
  return (rate / 100) * dutiableValue;
};

export const getTotalCustomsDuty = (
  exchangeRate,
  items,
  totalCustomsValue,
  totalFreight,
  totalInsurance,
  otherCharges
) => {
  var result = 0;

  items.map((row, key) => {
    var customsValue = row.customs_value;
    var customsValuePHP = customsValue * exchangeRate;
    var proRate = getProRateValue(customsValue, totalCustomsValue);
    var itemFreight =
      proRate * (removeCommaCurrency(totalFreight) * exchangeRate);
    var itemInsurance =
      proRate * (removeCommaCurrency(totalInsurance) * exchangeRate);
    var itemOthers =
      proRate * (removeCommaCurrency(otherCharges) * exchangeRate);
    var rate = row.hs_code !== null ? parseFloat(row.hs_code.percentage) : 0;

    var dutiableValue = getItemDutiableValue(
      customsValuePHP,
      itemFreight,
      itemInsurance,
      itemOthers
    );

    var customsDuty = getCustomsDuty(rate, dutiableValue);

    result += customsDuty;
  });

  return result;
};

export const getMarksString = (itemContainers) => {
  var marks = "";
  var qty20 = 0;
  var qty40 = 0;
  var qty45 = 0;

  itemContainers.map((row, key) => {
    if (row.deleted_at === null && row.container.deleted_at === null) {
      if (row.container.capacity === 20) {
        qty20 += 1;
      } else if (row.container.capacity === 40) {
        qty40 += 1;
      } else if (row.container.capacity === 45) {
        qty45 += 1;
      }
    }
  });

  if (qty20 > 0) {
    marks += `${qty20}X20`;
  }
  if (qty40 > 0) {
    marks += (marks !== "" ? "," : "") + `${qty40}X40`;
  }
  if (qty45 > 0) {
    marks += (marks !== "" ? "," : "") + `${qty45}X45`;
  }

  return marks;
};

/** ITEMS END **/
