import React, { useState, useEffect } from "react";

import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import { TablePagination } from "@material-ui/core";

import auth from "../../helpers/Auth";
import Modal from "./modal";

import DeleteRecord from "../../core/Delete";

import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.min.css";

const Index = () => {
  const [values, setValues] = useState({
    list: [],
    roles: [],
    rowsPerPage: 10,
    page: 0,
    total: 0,
  });

  const { list, rowsPerPage, page, total, roles } = values;

  useEffect(() => {
    fetchData();
  }, [page, rowsPerPage]);

  // function for get users api call
  const fetchData = async () => {
    const userData = auth.currentUser();
    const email = userData.emails[0];
    const apiPage = page + 1;

    const respUsers = await axios({
      method: "GET",
      url: `${process.env.REACT_APP_API}GetUsers?page=${apiPage}&per_page=${rowsPerPage}`,
      headers: {
        Authorization: "token",
        AccessEmail: email,
      },
    });

    const respRoles = await axios({
      method: "GET",
      url: `${process.env.REACT_APP_API}GetRoles`,
      headers: {
        Authorization: "token",
        AccessEmail: email,
      },
    });

    setValues({
      ...values,
      list: respUsers.data.data,
      total: respUsers.data.total,
      roles: respRoles.data.data,
    });
  };

  // function for changing rows per page
  const handleChangeRowsPerPage = (e) => {
    setValues({
      ...values,
      rowsPerPage: parseInt(e.target.value, 10),
      page: 0,
    });
  };

  // function for changing page
  const handleChangePage = (event, newPage) => {
    setValues({
      ...values,
      page: newPage,
    });
  };

  const deleteRecord = (id) => {
    const userData = auth.currentUser();
    const email = userData.emails[0];
    axios
      .delete(`${process.env.REACT_APP_API}DeleteUser?id=${id}`, {
        headers: {
          Authorization: "token",
          AccessEmail: email,
        },
      })
      .then((response) => {
        fetchData();
      })
      .catch((error) => {
        // console.log(error);
        toast.error(error.message);
      });
  };

  return (
    <TableContainer component={Paper} style={{ padding: "20px" }}>
      <Modal action="new" fetch={fetchData} roles={roles} id={0} />
      <ToastContainer />
      <Table className="tble" aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>ID</TableCell>
            <TableCell>First Name</TableCell>
            <TableCell>Last Name</TableCell>
            <TableCell>Email</TableCell>
            <TableCell>Company Name</TableCell>
            <TableCell>Tin Number</TableCell>
            <TableCell>Address</TableCell>
            <TableCell>Role</TableCell>
            <TableCell></TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {list.map((row) => (
            <TableRow key={row.id}>
              <TableCell component="th" scope="row">
                {row.id}
              </TableCell>
              <TableCell component="th" scope="row">
                {row.first_name}
              </TableCell>
              <TableCell component="th" scope="row">
                {row.last_name}
              </TableCell>
              <TableCell component="th" scope="row">
                {row.email}
              </TableCell>
              <TableCell component="th" scope="row">
                {row.company_name}
              </TableCell>
              <TableCell component="th" scope="row">
                {row.tin_number}
              </TableCell>
              <TableCell component="th" scope="row">
                {row.address}
              </TableCell>
              <TableCell component="th" scope="row">
                {row.role_name}
              </TableCell>
              <TableCell component="th" scope="row">
                <Modal
                  action="edit"
                  fetch={fetchData}
                  roles={roles}
                  id={row.id}
                />
                <DeleteRecord
                  fetch={fetchData}
                  id={row.id}
                  deleteRecord={deleteRecord}
                />
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
        <TablePagination
          count={total}
          page={page}
          onChangePage={handleChangePage}
          rowsPerPage={rowsPerPage}
          onChangeRowsPerPage={handleChangeRowsPerPage}
        />
      </Table>
    </TableContainer>
  );
};

export default Index;
