import React, { useState, useEffect } from "react";

import auth from "../helpers/Auth";

import axios from "axios";

const Initialize = () => {
  useEffect(() => {
    // get profile by authId
    fetchProfile();
  }, []);

  const fetchProfile = async () => {
    const userData = auth.currentUser();
    const email = userData.emails[0];
    const authId = auth.currentUser().id;

    const profile = await axios({
      method: "GET",
      url: `${process.env.REACT_APP_API}Profile?id=${authId}`,
      headers: {
        Authorization: "token",
        AccessEmail: email,
      },
    });

    var userProfile = profile.data.data;

    // sessionStorage.setItem("roleId", userProfile.user_role.role_id);

    window.location.href = "/declarations";
  };

  return <p>Please wait...</p>;
};

export default Initialize;
