export const displayCurrency = (string) => {
  return string.toString().replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
};

export const removeCommaCurrency = (string) => {
  return string.toString().replace(/[^\d\.\-]/g, "");
};

export const convertToCurrency = (num) => {
  return num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
};

export const formatter = new Intl.NumberFormat("en-US", {
  style: "currency",
  currency: "PHP",
  currencyDisplay: "symbol",
  minimumFractionDigits: 2,
});

export const currencyDisplay = (locale, currency, display, num) => {
  let formatted = new Intl.NumberFormat(locale, {
    style: "currency",
    currencyDisplay: display,
    currency: currency,
  }).format(num);

  if (formatted.startsWith("PHP")) {
    // temporary fix
    formatted = formatted.replace("PHP", "₱");
  }

  return formatted;
}