import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Modal from "@material-ui/core/Modal";
import Backdrop from "@material-ui/core/Backdrop";
import Fade from "@material-ui/core/Fade";
import Typography from "@material-ui/core/Typography";
import Checkbox from "@material-ui/core/Checkbox";
import FormControlLabel from "@material-ui/core/FormControlLabel";

import Grid from "@material-ui/core/Grid";

import CircularProgress from "@material-ui/core/CircularProgress";

import {
  Button,
  FormControl,
  TextField,
  Select,
  MenuItem,
} from "@material-ui/core";

import Autocomplete from "@material-ui/lab/Autocomplete";

import auth from "../../helpers/Auth";

import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.min.css";

import Icon from "@mdi/react";
import { mdiPlusBox } from "@mdi/js";

const useStyles = makeStyles((theme) => ({
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    borderRadius: "8px",
  },
  button: {
    // color: "#FFFFFF",
    // background: "#EA2121",
    // borderRadius: "4px",
    cursor: "pointer",
  },
  header: {
    padding: "19px",
    borderBottom: "1px solid rgba(33, 37, 41, 0.16)",
  },
  body: {
    padding: "19px",
    borderBottom: "1px solid rgba(33, 37, 41, 0.16)",
  },
  footer: {
    padding: "19px",
    textAlign: "right",
  },
  title: {
    fontWeight: "700",
  },
  esc: {
    float: "right",
    marginRight: "10px",
    fontSize: "8px",
    fontStyle: "normal",
    fontWeight: "400",
    lineHeight: "10px",
    letterSpacing: "0em",
    paddingTop: "7px",
    textAlign: "right",
  },
  escbtn: {
    border: "1px solid rgba(33, 37, 41, 0.16)",
    borderRadius: "4px",
    padding: "2px 6px",
  },
  close: {
    float: "right",
    cursor: "pointer",
  },
  text: {
    border: "1px solid rgba(33,37,41,.38)",
    boxSizing: "border-box",
    borderRadius: "4px",
    padding: "5px",
  },
  label: {
    fontSize: "12px",
    fontWeight: "500",
    marginBottom: "10px",
  },
  submit: {
    background: "#EA2121",
    "border-radius": "4px",
    color: "#FFFFFF",
    "font-size": "10px",
    "line-height": "14px",
    "min-width": "71px",
    "&:hover": {
      background: "#f00",
    },
    width: "120px",
    "& span": {
      "font-weight": "bold",
      textTransform: "capitalize",
    },
  },
  cancel: {
    border: "none",
    backgroundColor: "transparent",
    color: "#212529",
    textTransform: "capitalize",
    fontSize: "10px",
    marginRight: "10px",
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    // border: "2px solid #000",
    boxShadow: theme.shadows[5],
    minWidth: "40%",
    // padding: theme.spacing(2, 4, 3),
  },
}));

const Loading = <CircularProgress size={14} color="#FFFFFF" />;

export default function TransitionsModal({
  selectedImporter,
  importersList,
  modes,
  ports,
  fetch,
  history,
}) {
  const classes = useStyles();

  const [values, setValues] = useState({
    isOpen: false,
    blNumber: "",
    port: "",
    blList: [],
    port: "",
    declarationMode: "",
    registryNumber: "",
    blId: "",
    validationMessages: {},
    vehicleType: 1,
    category: "MVDP CBU Car",
    submitLabel: "Create",
    categories: [],
  });

  const {
    isOpen,
    blList,
    port,
    declarationMode,
    registryNumber,
    blId,
    validationMessages,
    vehicleType,
    category,
    submitLabel,
    categories,
  } = values;

  useEffect(() => {
    getBls();
  }, [vehicleType]);

  const handleOpen = () => {
    setValues({ ...values, isOpen: true });
  };

  const handleClose = () => {
    setValues({
      ...values,
      isOpen: false,
      declarationMode: "",
      validationMessages: {},
    });
  };

  const handleChange = (name) => (event) => {
    console.log(`${name} = ${event.target.value}`);
    setValues({ ...values, [name]: event.target.value });
  };

  const handleVehicleType = () => {
    var vType = vehicleType === 1 ? 0 : 1;
    setValues({ ...values, vehicleType: vType });
  };

  const getBls = async () => {
    const userData = auth.currentUser();
    const email = userData.emails[0];

    const respBls = await axios({
      method: "GET",
      url: `${process.env.REACT_APP_API}GetBillOfLadings?per_page=1000`,
      headers: {
        Authorization: "token",
        AccessEmail: email,
      },
    });

    const respCategories = await axios({
      method: "GET",
      url: `${process.env.REACT_APP_API}GetDeclarationCategories?per_page=1000&type=${vehicleType}`,
      headers: {
        Authorization: "token",
        AccessEmail: email,
      },
    });

    setValues({
      ...values,
      blList: respBls.data.data,
      categories: respCategories.data.data,
    });
  };

  const clickSubmit = () => {
    const userData = auth.currentUser();
    const email = userData.emails[0];

    setValues({
      ...values,
      submitLabel: Loading,
    });

    var formData = {
      importer: selectedImporter,
      registryNumber,
      vehicleType,
      category,
      declarationMode,
      port,
      blId,
    };
    axios
      .post(`${process.env.REACT_APP_API}CreateDeclaration`, formData, {
        headers: {
          Authorization: "token",
          AccessEmail: email,
        },
      })
      .then((response) => {
        // history.push(`declarations/${response.data.data.id}`);
        toast.success(response.data.message);
        fetch();
        handleClose();
        // // window.location.reload();
      })
      .catch((error) => {
        var validationErrors = error.response.data.validation;

        var validation = {};

        validation.registryNumber =
          validationErrors.registryNumber !== undefined &&
          validationErrors.registryNumber !== ""
            ? validationErrors.registryNumber
            : "";

        validation.vehicleType =
          validationErrors.vehicleType !== undefined &&
          validationErrors.vehicleType !== ""
            ? validationErrors.vehicleType
            : "";

        validation.category =
          validationErrors.category !== undefined &&
          validationErrors.category !== ""
            ? validationErrors.category
            : "";

        setValues({
          ...values,
          validationMessages: validation,
        });
      });
  };

  return (
    <div>
      <ToastContainer />
      {/* <Button className={classes.button} onClick={handleOpen}></Button> */}
      <Icon
        path={mdiPlusBox}
        className={classes.button}
        size={1.2}
        color="#EA2121"
        onClick={handleOpen}
      />
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={isOpen}
        width={1}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
          width: "40%",
        }}
      >
        <Fade in={isOpen}>
          <div className={classes.paper}>
            <div className={classes.header}>
              <span className={classes.title}>New Import Declaration</span>
              <span className={classes.close} onClick={handleClose}>
                x
              </span>
              <span className={classes.esc}>
                Press <span className={classes.escbtn}>ESC</span>
              </span>
            </div>
            <div className={classes.body}>
              <Grid container spacing={3}>
                <Grid item xs={12} sm={12}>
                  <FormControl fullWidth>
                    <Typography className={classes.label}>
                      Registry No. (Optional)
                    </Typography>
                    <Autocomplete
                      id="combo-box-demo"
                      options={blList}
                      getOptionLabel={(option) => option.registry_number}
                      renderInput={(params) => (
                        <TextField
                          {...params}
                          variant="outlined"
                          helperText={validationMessages.registryNumber}
                          error={
                            validationMessages.registryNumber !== undefined
                          }
                        />
                      )}
                      onChange={(event, newValue) => {
                        if (newValue) {
                          setValues({
                            ...values,
                            registryNumber: newValue.registry_number,
                            port: newValue.port.id,
                            declarationMode: newValue.declaration_model_id,
                            blId: newValue.id,
                          });
                        }
                      }}
                    />
                  </FormControl>
                </Grid>
              </Grid>
              <Grid container spacing={3}>
                <Grid item xs={12} sm={6}>
                  <FormControl fullWidth>
                    <Typography className={classes.label}>
                      Vehicle Type
                    </Typography>
                    <FormControlLabel
                      value="end"
                      control={
                        <Checkbox
                          color="primary"
                          style={{
                            color: "#EA2121",
                          }}
                          checked={vehicleType === 1 ? true : false}
                          onClick={handleVehicleType}
                        />
                      }
                      label="Brand New"
                      labelPlacement="start"
                      style={{
                        marginLeft: "0",
                        border: "1px solid rgb(33,37,41,0.38)",
                        boxSizing: "border-box",
                        borderRadius: "4px",
                      }}
                    />
                  </FormControl>
                </Grid>
                <Grid item xs={12} sm={6}>
                  <FormControl size="small" fullWidth>
                    <Typography className={classes.label}>Category</Typography>
                    <Select
                      variant="outlined"
                      margin="normal"
                      id="category"
                      name="category"
                      autoFocus
                      onChange={handleChange("category")}
                      InputProps={{
                        className: "ereg-input",
                      }}
                      style={{
                        padding: "13px !important",
                      }}
                    >
                      <MenuItem value="">-- Select Category --</MenuItem>
                      {categories.map((row) => (
                        <MenuItem key={row.id} value={row.id}>
                          {row.name}
                        </MenuItem>
                      ))}
                    </Select>
                  </FormControl>
                </Grid>
              </Grid>
            </div>
            <div className={classes.footer}>
              <Button className={classes.cancel} onClick={handleClose}>
                Cancel
              </Button>
              <Button className={classes.submit} onClick={clickSubmit}>
                {submitLabel}
              </Button>
            </div>
          </div>
        </Fade>
      </Modal>
    </div>
  );
}
