import React, { useState, useEffect } from "react";

import { Select, MenuItem, TextField } from "@material-ui/core";

import { withStyles } from "@material-ui/core/styles";

import Autocomplete from "@material-ui/lab/Autocomplete";

import auth from "../../helpers/Auth";

import axios from "axios";

// start of component
const PackagingCodes = ({ value, setValue }) => {
  const [values, setValues] = useState({
    packagingCodes: [],
  });

  const { packagingCodes } = values;

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    const userData = auth.currentUser();
    const email = userData.emails[0];

    const respPackagingCodes = await axios({
      method: "GET",
      url: `${process.env.REACT_APP_API}GetPackagingCodes?per_page=1000`,
      headers: {
        Authorization: "token",
        AccessEmail: email,
      },
    });

    setValues({
      ...values,
      packagingCodes: respPackagingCodes.data.data,
    });
  };

  return (
    <Autocomplete
      // value={selectedCE}
      id="combo-box-demo"
      options={packagingCodes}
      getOptionLabel={(option) => `[${option.code}] ${option.name}`}
      autoComplete="off"
      name="packagingCode"
      className="ereg-input"
      renderInput={(params) => (
        <TextField
          {...params}
          InputProps={{
            ...params.InputProps,
            disableUnderline: true,
          }}
          name="packagingCode"
        />
      )}
      onChange={(event, newValue) => {
        var newPackageCodeId = 0;
        if (newValue) {
          newPackageCodeId = newValue.id;
        }
        setValue("pacakageCodeId", newPackageCodeId);
      }}
    />
  );
};

export default PackagingCodes;
