    const userData = auth.currentUser();
    const email = userData.emails[0];
    var formData = {
      fields: [
        {
          name,
          value: event.target.value,
        },
      ],
    };
    console.log(formData);
    axios
      .patch(`${process.env.REACT_APP_API}UpdateContainer?id=${id}`, formData, {
        headers: {
          Authorization: "token",
          AccessEmail: email,
        },
      })
      .then((response) => {
        getContainersMain();
      });
