import React from "react";

import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import { Button } from "@material-ui/core";

import auth from "../../helpers/Auth";

import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.min.css";

const TableComp = ({ list }) => {

  const sendNotif = (id) => {
    const userData = auth.currentUser();
    const email = userData.emails[0];

    const formData = {
        notifId: id
    };

    axios
      .post(`${process.env.REACT_APP_API}SendNotification`, formData, {
        headers: {
          Authorization: "token",
          AccessEmail: email,
        },
      })
      .then((response) => {
        toast.success(response.data.message);
      })
      .catch((error) => {
        // console.log(error);
        toast.error(error.message);
      });
  };

  return (
    <TableContainer component={Paper}>
      <ToastContainer />
      <Table className="tble" aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>Title</TableCell>
            <TableCell>Content</TableCell>
            <TableCell>Sender</TableCell>
            <TableCell>Registry Number</TableCell>
            <TableCell></TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {list.map((row) => (
            <TableRow key={row.id}>
              <TableCell component="th" scope="row">
                {row.title}
              </TableCell>
              <TableCell component="th" scope="row">
                {row.content}
              </TableCell>
              <TableCell component="th" scope="row">
                {row.sender}
              </TableCell>
              <TableCell component="th" scope="row">
                {row.registry_number}
              </TableCell>
              <TableCell component="th" scope="row">
                <Button
                  variant="contained"
                  color="primary"
                  onClick={() => sendNotif(row.id)}
                >
                  Send
                </Button>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
};

export default TableComp;
