import React from "react";
import { BrowserRouter, Switch, Route } from "react-router-dom";
import Declarations from "./components/Declarations/index";
import DeclarationForm from "./components/Declarations/form";
import Dashboard from "./components/Dashboard";
import Documents from "./components/Documents";
import Inbox from "./components/Inbox/index";
import NotificationsManagement from "./components/NotificationsManagement/index";
import Roles from "./components/Roles/index";
import Users from "./components/Users/index";
import Register from "./components/Register/index";

import Layout from "./core/Sidebar";
import Initialize from "./core/Initialize";

import authentication from "@kdpw/msal-b2c-react";

authentication.initialize({
  instance: process.env.REACT_APP_INSTANCE,
  tenant: process.env.REACT_APP_TENANT,
  signInPolicy: process.env.REACT_APP_SIGNIN,
  applicationId: process.env.REACT_APP_APPID,
  cacheLocation: "sessionStorage",
  scopes: ["profile", "openid"],
  redirectUri: process.env.REACT_APP_URI,
  postLogoutRedirectUri: process.env.REACT_APP_URI,
  validateAuthority: false,
});

const Routes = () => {
  return (
    <BrowserRouter>
      <Layout>
        <Switch>
          <Route
            path="/"
            exact
            component={(Initialize)}
          />
          <Route
            path="/dashboard"
            exact
            component={(Dashboard)}
          />
          <Route
            path="/declarations"
            exact
            component={(Declarations)}
          />
          <Route
            path="/documents"
            exact
            component={(Documents)}
          />
          <Route
            path="/inbox"
            exact
            component={(Inbox)}
          />
          <Route
            path="/roles"
            exact
            component={(Roles)}
          />
          <Route
            path="/users"
            exact
            component={(Users)}
          />
          <Route
            path="/notifications-management"
            exact
            component={(NotificationsManagement)}
          />
          <Route
            path="/declarations/:id"
            exact
            component={(DeclarationForm)}
          />
          <Route path="/register" exact component={Register} />
        </Switch>
      </Layout>
    </BrowserRouter>
  );
};
export default Routes;
