import React, { useEffect } from "react";

import {
  MenuItem,
  Typography,
  List,
  ListItem,
  Divider,
  ListItemText,
  ListItemAvatar,
  Avatar,
  Box,
} from "@material-ui/core";

import Icon from "@mdi/react";
import { mdiBell } from "@mdi/js";

import { makeStyles } from "@material-ui/core/styles";
import Modal from "@material-ui/core/Modal";
import Backdrop from "@material-ui/core/Backdrop";
import Fade from "@material-ui/core/Fade";

import auth from "../helpers/Auth";
import axios from "axios";

import Moment from "react-moment";
import "moment-timezone";

const signalR = require("@aspnet/signalr");

const useStyles = makeStyles((theme) => ({
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    // border: "2px solid #000",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
  root: {
    width: "100%",
    backgroundColor: theme.palette.background.paper,
  },
  inline: {
    display: "inline",
  },
}));

const Notifications = () => {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const [count, setCount] = React.useState(0);
  const [list, setList] = React.useState([]);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  useEffect(() => {
    const setConnection = async () => {
      // build hub connection to API
      const connection = new signalR.HubConnectionBuilder()
        .withUrl(`${process.env.REACT_APP_API}`)
        .configureLogging(signalR.LogLevel.Information)
        .build();

      // start connection
      await connection.start();
      console.log("connected");

      // listen for new messages
      await connection.on("newMessage", (data) => {
        // setCount(Math.floor(Math.random() * 100));
        getNotifs();
      });
    };

    const getNotifs = async () => {
      const userData = auth.currentUser();
      const email = userData.emails[0];
      const respNotifs = await axios({
        method: "GET",
        url: `${process.env.REACT_APP_API}UserNotifications?per_page=1000`,
        headers: {
          Authorization: "token",
          AccessEmail: email,
        },
      });
      setCount(respNotifs.data.unread);
      setList(respNotifs.data.data);
    };

    getNotifs();
    setConnection();
  }, []);

  return (
    <div>
      <MenuItem onClick={handleOpen}>
        <Icon path={mdiBell} size={1} />
        &nbsp; Notifications
        <Typography className="right-elem">{count}</Typography>
      </MenuItem>
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={open}>
          <div className={classes.paper}>
            <h2 id="transition-modal-title">
              You have {count} unread notification(s)
            </h2>
            <List className={classes.root}>
              {list.map((row) => (
                <ListItem key={row.id}>
                  <Box className="left-elem">
                    <Typography component="span" display="block">
                      {row.content}
                    </Typography>
                    <Typography component="span">{row.sender}</Typography>
                    <Typography component="span"> • </Typography>
                    <Typography component="span">
                      {row.registry_number}
                    </Typography>
                  </Box>
                  <Box className="right-elem">
                    <Typography component="span" className="right-elem">
                      <Moment>{row.sent_at}</Moment>
                    </Typography>
                  </Box>
                </ListItem>
              ))}
            </List>
          </div>
        </Fade>
      </Modal>
    </div>
  );
};

export default Notifications;
