import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Modal from "@material-ui/core/Modal";
import Backdrop from "@material-ui/core/Backdrop";
import Fade from "@material-ui/core/Fade";
import {
  Button,
  FormControl,
  TextField,
  Select,
  MenuItem,
} from "@material-ui/core";

import auth from "../../helpers/Auth";

import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.min.css";

const useStyles = makeStyles((theme) => ({
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    // border: "2px solid #000",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));

export default function TransitionsModal({ users }) {
  const classes = useStyles();

  const [values, setValues] = useState({
    isOpen: false,
    title: "",
    content: "",
    sender: "",
    registry: "",
    user: "",
  });

  const { isOpen, title, content, sender, registry, user } = values;

  const handleOpen = () => {
    setValues({ ...values, isOpen: true });
  };

  const handleClose = () => {
    setValues({ ...values, isOpen: false });
  };

  const handleChange = (name) => (event) => {
    setValues({ ...values, [name]: event.target.value });
  };

  const clickSubmit = () => {
    const userData = auth.currentUser();
    const email = userData.emails[0];
    var recipients = [];
    recipients.push(user);
    var formData = {
      title,
      content,
      sender,
      registryNumber: registry,
      recipients,
    };
    axios
      .post(`${process.env.REACT_APP_API}CreateNotification`, formData, {
        headers: {
          Authorization: "token",
          AccessEmail: email,
        },
      })
      .then((response) => {
        toast.success(response.data.message);
        window.location.reload();
      })
      .catch((error) => {
        // console.log(error);
        toast.error(error.message);
      });
  };

  return (
    <div>
      <ToastContainer />
      <Button variant="outlined" color="primary" onClick={handleOpen}>
        New Notification
      </Button>
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={isOpen}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={isOpen}>
          <div className={classes.paper}>
            <h2 id="transition-modal-title">Create Notification</h2>
            <FormControl fullWidth>
              <TextField
                variant="outlined"
                margin="normal"
                id="title"
                label="Title"
                name="title"
                autoComplete="title"
                autoFocus
                onChange={handleChange("title")}
              />
            </FormControl>
            <FormControl fullWidth>
              <TextField
                variant="outlined"
                margin="normal"
                id="content"
                label="Content"
                name="content"
                multiline
                rows={5}
                rowsMax={10}
                onChange={handleChange("content")}
              />
            </FormControl>
            <FormControl fullWidth>
              <TextField
                variant="outlined"
                margin="normal"
                id="sender"
                label="Sender"
                name="sender"
                autoComplete="sender"
                onChange={handleChange("sender")}
              />
            </FormControl>
            <FormControl fullWidth>
              <TextField
                variant="outlined"
                margin="normal"
                id="registry"
                label="Registry Number"
                name="registry"
                autoComplete="registry"
                onChange={handleChange("registry")}
              />
            </FormControl>
            <FormControl fullWidth>
              <Select
                variant="outlined"
                margin="normal"
                id="user"
                label="Recipient"
                name="user"
                onChange={handleChange("user")}
              >
                <MenuItem value="" selected={true}>
                  -- Select Recipient --
                </MenuItem>
                {users.map((row) => (
                  <MenuItem key={row.id} value={row.id}>
                    {row.email}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
            <FormControl className="mt-10" variant="outlined">
              <Button variant="outlined" onClick={clickSubmit}>
                Submit
              </Button>
            </FormControl>
          </div>
        </Fade>
      </Modal>
    </div>
  );
}
