import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Modal from "@material-ui/core/Modal";
import Backdrop from "@material-ui/core/Backdrop";
import Fade from "@material-ui/core/Fade";
import {
  Button,
  FormControl,
  TextField,
  Select,
  MenuItem,
} from "@material-ui/core";

import Icon from "@mdi/react";
import { mdiPen } from "@mdi/js";

import auth from "../../helpers/Auth";

import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.min.css";

const useStyles = makeStyles((theme) => ({
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    // border: "2px solid #000",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));

export default function TransitionsModal({ action, fetch, roles, id }) {
  const classes = useStyles();

  const [values, setValues] = useState({
    isOpen: false,
    firstName: "",
    firstNameError: "",
    lastName: "",
    lastNameError: "",
    requestEmail: "",
    emailError: "",
    role: 0,
    password: "",
    passwordError: "",
    companyName: "",
    companyNameError: "",
    tinNumber: "",
    tinNumberError: "",
    address: "",
    addressError: "",
  });

  const {
    isOpen,
    firstName,
    firstNameError,
    lastName,
    lastNameError,
    requestEmail,
    emailError,
    role,
    password,
    passwordError,
    companyName,
    companyNameError,
    tinNumber,
    tinNumberError,
    address,
    addressError,
  } = values;

  const handleOpen = () => {
    if (id !== 0 && action === "edit") {
      // get user profile
      fetchProfile();
    } else {
      setValues({ ...values, isOpen: true });
    }
  };

  // function for user profile
  const fetchProfile = async () => {
    const userData = auth.currentUser();
    const email = userData.emails[0];

    const respProfile = await axios({
      method: "GET",
      url: `${process.env.REACT_APP_API}GetUser?id=${id}`,
      headers: {
        Authorization: "token",
        AccessEmail: email,
      },
    });

    var profile = respProfile.data.data;

    setValues({
      ...values,
      isOpen: true,
      firstName: profile.first_name,
      lastName: profile.last_name,
      requestEmail: profile.email,
      role: profile.role,
      companyName: profile.company_name,
      tinNumber: profile.tin_number,
      address: profile.address,
    });
  };

  const handleClose = () => {
    setValues({
      ...values,
      isOpen: false,
      firstName: "",
      firstNameError: "",
      lastName: "",
      lastNameError: "",
      requestEmail: "",
      emailError: "",
      role: 1,
      password: "",
      passwordError: "",
      companyName: "",
      companyNameError: "",
      tinNumber: "",
      tinNumberError: "",
      address: "",
      addressError: "",
    });
  };

  const handleChange = (name) => (event) => {
    setValues({ ...values, [name]: event.target.value });
  };

  const clickSubmit = () => {
    var formData = {
      firstName,
      lastName,
      role,
      email: requestEmail,
      password,
      companyName,
      tinNumber,
      address,
    };
    if (action === "new") {
      createUser(formData);
    } else {
      updateUser(formData, id);
    }
  };

  const createUser = (formData) => {
    const userData = auth.currentUser();
    const email = userData.emails[0];
    axios
      .post(`${process.env.REACT_APP_API}CreateUser`, formData, {
        headers: {
          Authorization: "token",
          AccessEmail: email,
        },
      })
      .then((response) => {
        toast.success(response.data.message);
        fetch();
        handleClose();
        // window.location.reload();
      })
      .catch((error) => {
        console.log(error.response);
        var validationErrors = error.response.data.validation;
        // toast.success(error.response.data.error);
        setValues({
          ...values,
          firstNameError:
            validationErrors !== undefined ? validationErrors.firstName : "",
          lastNameError:
            validationErrors !== undefined ? validationErrors.lastName : "",
          emailError:
            validationErrors !== undefined ? validationErrors.email : "",
          passwordError:
            validationErrors !== undefined
              ? validationErrors.password
              : error.response.data.error,
          companyNameError:
            validationErrors !== undefined ? validationErrors.companyName : "",
          tinNumberError:
            validationErrors !== undefined ? validationErrors.tinNumber : "",
          addressError:
            validationErrors !== undefined ? validationErrors.address : "",
        });
      });
  };

  const updateUser = (formData, id) => {
    const userData = auth.currentUser();
    const email = userData.emails[0];
    axios
      .patch(`${process.env.REACT_APP_API}UpdateUser?id=${id}`, formData, {
        headers: {
          Authorization: "token",
          AccessEmail: email,
        },
      })
      .then((response) => {
        toast.success(response.data.message);
        fetch();
        handleClose();
        // window.location.reload();
      })
      .catch((error) => {
        console.log(error.response);
        var validationErrors = error.response.data.validation;
        // toast.success(error.response.data.error);
        setValues({
          ...values,
          firstNameError:
            validationErrors !== undefined ? validationErrors.firstName : "",
          lastNameError:
            validationErrors !== undefined ? validationErrors.lastName : "",
          emailError:
            validationErrors !== undefined ? validationErrors.email : "",
          passwordError:
            validationErrors !== undefined
              ? validationErrors.password
              : error.response.data.error,
          companyNameError:
            validationErrors !== undefined ? validationErrors.companyName : "",
          tinNumberError:
            validationErrors !== undefined ? validationErrors.tinNumber : "",
          addressError:
            validationErrors !== undefined ? validationErrors.address : "",
        });
      });
  };

  return (
    <div>
      <ToastContainer />
      <Button variant="outlined" color="primary" onClick={handleOpen}>
        {action === "new" ? "Create User" : <Icon path={mdiPen} size={1} />}
      </Button>
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={isOpen}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={isOpen}>
          <div className={classes.paper}>
            <h2 id="transition-modal-title">User Account</h2>
            <FormControl fullWidth>
              <Select
                variant="outlined"
                margin="normal"
                id="role"
                label="Role"
                name="role"
                autoFocus
                onChange={handleChange("role")}
                defaultValue={role}
              >
                <MenuItem value="" selected={true}>
                  -- Select Role --
                </MenuItem>
                {roles.map((row) => (
                  <MenuItem key={row.id} value={row.id}>
                    {row.name}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
            <FormControl fullWidth>
              <TextField
                variant="outlined"
                margin="normal"
                id="firstName"
                label="First Name"
                name="firstName"
                autoComplete="firstName"
                onChange={handleChange("firstName")}
                helperText={firstNameError}
                error={firstNameError !== "" ? true : false}
                value={firstName}
              />
            </FormControl>
            <FormControl fullWidth>
              <TextField
                variant="outlined"
                margin="normal"
                id="lastName"
                label="Last Name"
                name="lastName"
                autoComplete="lastName"
                onChange={handleChange("lastName")}
                helperText={lastNameError}
                error={lastNameError !== "" ? true : false}
                value={lastName}
              />
            </FormControl>
            <FormControl fullWidth>
              <TextField
                variant="outlined"
                margin="normal"
                id="requestEmail"
                label="Email"
                name="requestEmail"
                autoComplete="requestEmail"
                onChange={handleChange("requestEmail")}
                helperText={emailError}
                error={emailError !== "" ? true : false}
                value={requestEmail}
              />
            </FormControl>
            <FormControl fullWidth>
              <TextField
                variant="outlined"
                margin="normal"
                id="companyName"
                label="Company Name"
                name="companyName"
                autoComplete="companyName"
                onChange={handleChange("companyName")}
                helperText={companyNameError}
                error={companyNameError !== "" ? true : false}
                value={companyName}
              />
            </FormControl>
            <FormControl fullWidth>
              <TextField
                variant="outlined"
                margin="normal"
                id="tinNumber"
                label="Tin Number"
                name="tinNumber"
                autoComplete="tinNumber"
                onChange={handleChange("tinNumber")}
                helperText={tinNumberError}
                error={tinNumberError !== "" ? true : false}
                value={tinNumber}
              />
            </FormControl>
            <FormControl fullWidth>
              <TextField
                variant="outlined"
                margin="normal"
                id="address"
                label="Address"
                name="address"
                autoComplete="address"
                onChange={handleChange("address")}
                helperText={addressError}
                error={addressError !== "" ? true : false}
                value={address}
                multiline
                rows={5}
                rowsMax={10}
              />
            </FormControl>
            <FormControl fullWidth>
              <TextField
                variant="outlined"
                margin="normal"
                id="password"
                label="Password"
                name="password"
                autoComplete="password"
                onChange={handleChange("password")}
                helperText={passwordError}
                error={passwordError !== "" ? true : false}
              />
            </FormControl>
            <FormControl className="mt-10" variant="outlined">
              <Button variant="outlined" onClick={clickSubmit}>
                Submit
              </Button>
            </FormControl>
          </div>
        </Fade>
      </Modal>
    </div>
  );
}
