import React, { useState, useEffect, Fragment } from "react";
import axios from "axios";
import {
  Grid,
  TextField,
  FormControl,
  Typography,
  InputLabel,
  Select,
  MenuItem,
  Button,
} from "@material-ui/core";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.min.css";

const Documents = ({ match, history }) => {
  const [values, setValues] = useState({
    list: [],
    name: "",
    type: 0,
    file: "",
  });

  const { list, name, type, file } = values;

  useEffect(() => {
    loadDocuments();
  }, []);

  const loadDocuments = () => {
    axios({
      method: "GET",
      url: `http://localhost:7071/api/GetDocuments`,
      headers: {
        Authorization: "token",
        AccessEmail: "lorenz@e-reg.ph",
      },
    })
      .then((response) => {
        setValues({ ...values, list: response.data.data });
      })
      .catch((error) => {
        console.log("Documents error", error);
      });
  };

  const handleChange = (name) => (event) => {
    console.log(event.target.value);
    setValues({ ...values, [name]: event.target.value });
  };

  const onChange = () => (e) => {
    this.setState({ file: e.target.files[0] });
  };

  const uploadForm = () => (
    <Grid container className="mt-30" style={{ padding: "20px" }}>
      <Grid item xs={12} sm={6}>
        <ToastContainer />

        <Typography component="h1" variant="h5">
          Upload a Document
        </Typography>
        <form className="docs-form" noValidate autoComplete="off">
          <FormControl fullWidth>
            <TextField
              variant="outlined"
              margin="normal"
              id="name"
              label="Name"
              name="name"
              autoComplete="name"
              autoFocus
              onChange={handleChange("name")}
            />
          </FormControl>

          <FormControl variant="outlined" fullWidth>
            <InputLabel>Type</InputLabel>
            <Select label="Type" onChange={handleChange("type")}>
              <MenuItem value="1">Bill of Lading</MenuItem>
              <MenuItem value="2">Temporary SAD</MenuItem>
              <MenuItem value="3">Final SAD</MenuItem>
              <MenuItem value="4">Packing List</MenuItem>
              <MenuItem value="5">Sales Invoice</MenuItem>
            </Select>
          </FormControl>

          <FormControl className="mt-10" variant="outlined" fullWidth>
            <input
              type="file"
              id="file"
              onChange={onChange}
              accept="application/pdf"
            />
          </FormControl>

          <FormControl className="mt-10" variant="outlined">
            <Button variant="outlined" onClick={clickSubmit}>
              Submit
            </Button>
          </FormControl>
        </form>
      </Grid>
    </Grid>
  );

  const clickSubmit = (event) => {
    event.preventDefault();
    // submit to backend
    const formData = new FormData();
    var uploadFile = document.querySelector("#file");
    formData.append("file", uploadFile.files[0]);

    axios
      .post(`http://localhost:7071/api/CreateDocument`, formData, {
        headers: {
          "Content-Type": "multipart/form-data",
          Authorization: "token",
          AccessEmail: "lorenz@e-reg.ph",
          name,
          type,
        },
      })
      .then((response) => {
        toast.success(response.data.message);
        loadDocuments();
      })
      .catch((error) => {
        // console.log(error);
        toast.error(error.message);
      });
  };

  const docsList = () => (
    <Grid container style={{ padding: "20px" }}>
      {list.map((doc, index) => (
        <Grid item xs={12} sm={3}>
          <div key={index} className="doc-wrapper">
            <div className="placeholder"></div>
            <div className="details">
              <h3>
                Name: {doc.name} v{doc.version}
              </h3>
              <p>Type: {doc.type}</p>
              <p>
                <a
                  href={`https://eregstoragedev2.blob.core.windows.net/documents/${doc.uploaded_file}`}
                  download
                >
                  Download
                </a>
              </p>
            </div>
          </div>
        </Grid>
      ))}
    </Grid>
  );

  return (
    <Fragment>
      {docsList()}
      {uploadForm()}
    </Fragment>
  );
};

export default Documents;
