import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Modal from "@material-ui/core/Modal";
import Backdrop from "@material-ui/core/Backdrop";
import Fade from "@material-ui/core/Fade";
import {
  Button,
  FormControl,
  TextField,
} from "@material-ui/core";

import auth from "../../helpers/Auth";

import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.min.css";

const useStyles = makeStyles((theme) => ({
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    // border: "2px solid #000",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));

export default function TransitionsModal({ fetch }) {
  const classes = useStyles();

  const [values, setValues] = useState({
    isOpen: false,
    name: "",
    nameError: "",
    description: "",
    descriptionError: "",
  });

  const { isOpen, name, description, nameError, descriptionError } = values;

  const handleOpen = () => {
    setValues({ ...values, isOpen: true });
  };

  const handleClose = () => {
    setValues({
      ...values,
      isOpen: false,
      nameError: "",
      descriptionError: "",
      name: "",
      description: "",
    });
  };

  const handleChange = (name) => (event) => {
    setValues({ ...values, [name]: event.target.value });
  };

  const clickSubmit = () => {
    const userData = auth.currentUser();
    const email = userData.emails[0];
    var formData = {
      name,
      description,
    };
    axios
      .post(`${process.env.REACT_APP_API}CreateRole`, formData, {
        headers: {
          Authorization: "token",
          AccessEmail: email,
        },
      })
      .then((response) => {
        toast.success(response.data.message);
        fetch();
        handleClose();
        // window.location.reload();
      })
      .catch((error) => {
        var validationErrors = error.response.data.validation;
        setValues({
          ...values,
          nameError: validationErrors.name,
          descriptionError: validationErrors.description,
        });
      });
  };

  return (
    <div>
      <ToastContainer />
      <Button variant="outlined" color="primary" onClick={handleOpen}>
        New Role
      </Button>
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={isOpen}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={isOpen}>
          <div className={classes.paper}>
            <h2 id="transition-modal-title">Create Role</h2>
            <FormControl fullWidth>
              <TextField
                variant="outlined"
                margin="normal"
                id="name"
                label="Name"
                name="name"
                autoComplete="name"
                autoFocus
                onChange={handleChange("name")}
                helperText={nameError}
                error={nameError !== "" ? true : false}
              />
            </FormControl>
            <FormControl fullWidth>
              <TextField
                variant="outlined"
                margin="normal"
                id="description"
                label="Description"
                name="description"
                multiline
                rows={5}
                rowsMax={10}
                onChange={handleChange("description")}
                helperText={descriptionError}
                error={descriptionError !== "" ? true : false}
              />
            </FormControl>
            <FormControl className="mt-10" variant="outlined">
              <Button variant="outlined" onClick={clickSubmit}>
                Submit
              </Button>
            </FormControl>
          </div>
        </Fade>
      </Modal>
    </div>
  );
}
