import React, { Component } from "react";
import { Link, withRouter } from "react-router-dom";
import {
  AppBar,
  Toolbar,
  IconButton,
  Typography,
  Hidden,
  Drawer,
  CssBaseline,
  MenuList,
  MenuItem,
  FormControlLabel,
  Switch,
} from "@material-ui/core";
import {
  withStyles,
  createMuiTheme,
  ThemeProvider,
} from "@material-ui/core/styles";
import { Menu } from "@material-ui/icons";
import { compose } from "recompose";
import Icon from "@mdi/react";
import {
  mdiViewDashboard,
  mdiTextBox,
  mdiAccountMultiple,
  mdiTextBoxMultiple,
  mdiCog,
  mdiMoonWaningCrescent,
  mdiLogout,
  mdiInbox,
  mdiBell,
} from "@mdi/js";
import { getInitialMode } from "../helpers/Theme";

import {
  orange,
  lightBlue,
  deepPurple,
  deepOrange,
} from "@material-ui/core/colors";

import Notifications from "./Notifications";

import Auth from "../helpers/Auth";

const drawerWidth = 300;

const styles = (theme) => ({
  root: {
    flexGrow: 1,
    zIndex: 1,
    overflow: "hidden",
    position: "relative",
    display: "flex",
    width: "100%",
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
  },
  navIconHide: {
    [theme.breakpoints.up("md")]: {
      display: "none",
    },
  },
  // toolbar: theme.mixins.toolbar,
  toolbar: {
    paddingTop: "19px",
    paddingBottom: "25px",
    fontWeight: "bold",
    borderBottom: "1px solid rgba(33, 37, 41, 0.16)",
    textAlign: "center",
    fontSize: "14px",
  },
  drawerPaper: {
    width: drawerWidth,
    [theme.breakpoints.up("md")]: {
      position: "relative",
    },
  },
  content: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.default,
    padding: theme.spacing(3),
  },
  nested: {
    paddingLeft: theme.spacing(4),
  },
});

class Layout extends Component {
  state = {
    mobileOpen: false,
    darkMode: getInitialMode(),
  };

  handleDrawerToggle = () => {
    this.setState({ mobileOpen: !this.state.mobileOpen });
  };

  handleDarkModeToggle = () => {
    const darkModeState = !this.state.darkMode; // set value to opposite of current dark mode state
    // console.log('New state!', darkModeState)
    this.setState({ darkMode: darkModeState }); // set new value to dark mode state
    localStorage.setItem("dark", JSON.stringify(darkModeState)); // save to local storage
    // console.log('DARK MODE', this.state.darkMode)
  };

  render() {
    const {
      classes,
      location: { pathname },
      children,
    } = this.props;
    const { mobileOpen } = this.state;
    const darkMode = getInitialMode();

    const drawer = (
      <div>
        <Hidden smDown>
          <div className={classes.toolbar}>Customs Brokerage</div>
        </Hidden>
        <MenuList>
          <div className="menu-group">
            <MenuItem
              className="menu-links"
              component={Link}
              to="/dashboard"
              selected={"/dashboard" === pathname || "/" === pathname}
            >
              <Icon path={mdiViewDashboard} size={1} />
              &nbsp; Dashboard
            </MenuItem>

            <MenuItem
              className="menu-links"
              component={Link}
              to="/inbox"
              selected={"/inbox" === pathname || "/inbox" === pathname}
            >
              <Icon path={mdiInbox} size={1} />
              &nbsp; Inbox
            </MenuItem>

            <Notifications />

            <MenuItem
              className="menu-links"
              component={Link}
              to="/declarations"
              selected={"/declarations" === pathname}
            >
              <Icon path={mdiTextBox} size={1} />
              &nbsp; Declarations
            </MenuItem>

            <MenuItem
              className="menu-links"
              component={Link}
              to="/roles"
              selected={"/roles" === pathname}
            >
              <Icon path={mdiAccountMultiple} size={1} />
              &nbsp; User Roles
            </MenuItem>

            <MenuItem
              className="menu-links"
              component={Link}
              to="/users"
              selected={"/users" === pathname}
            >
              <Icon path={mdiAccountMultiple} size={1} />
              &nbsp; Users
            </MenuItem>
          </div>

          <div className="menu-group">
            <MenuItem
              className="menu-links"
              component={Link}
              to="/documents"
              selected={"/documents" === pathname}
            >
              <Icon path={mdiTextBoxMultiple} size={1} />
              &nbsp; Documents
            </MenuItem>
          </div>

          <div className="menu-group footer">
            <MenuItem
              className="menu-links"
              component={Link}
              to="/notifications-management"
              selected={"/notifications-management" === pathname}
            >
              <Icon path={mdiBell} size={1} />
              &nbsp; Notifications Management
            </MenuItem>
            <MenuItem
              className="menu-links"
              onClick={() =>
                (window.location.href =
                  "https://eregb2cdev.b2clogin.com/eregb2cdev.onmicrosoft.com/oauth2/v2.0/authorize?p=B2C_1_Update_Profile&client_id=8e237859-3907-4f03-a775-b728cd80fc91&nonce=defaultNonce&redirect_uri=http%3A%2F%2Flocalhost%3A3000%2F&scope=openid&response_type=id_token&prompt=login")
              }
              selected={"/settings" === pathname}
            >
              <Icon path={mdiCog} size={1} />
              &nbsp; Profile
            </MenuItem>
            <MenuItem className="menu-links static">
              <Icon path={mdiMoonWaningCrescent} size={1} />
              &nbsp; Dark Mode
              <Typography className="right-elem">
                <FormControlLabel
                  control={<Switch size="small" />}
                  onChange={this.handleDarkModeToggle}
                  checked={darkMode}
                />
              </Typography>
            </MenuItem>
            <MenuItem
              className="menu-links static"
              onClick={() => Auth.logout()}
            >
              <Icon path={mdiLogout} size={1} />
              &nbsp; Logout
            </MenuItem>
          </div>
        </MenuList>
      </div>
    );

    const palletType = darkMode ? "dark" : "light";
    const mainPrimaryColor = darkMode ? orange[500] : lightBlue[500];
    const mainSecondaryColor = darkMode ? deepOrange[900] : deepPurple[500];
    const darkTheme = createMuiTheme({
      palette: {
        type: palletType,
        primary: {
          main: mainPrimaryColor,
        },
        secondary: {
          main: mainSecondaryColor,
        },
      },
    });

    return (
      <ThemeProvider theme={darkTheme}>
        <div
          id="main-wrapper"
          className={darkMode ? "dark-mode" : "light-mode"}
          style={{ height: "100vh" }}
        >
          <CssBaseline />
          <div className={classes.root}>
            {/* <AppBar position="absolute" className={classes.appBar}>
              <Toolbar>
                <IconButton
                  color="inherit"
                  aria-label="open drawer"
                  onClick={this.handleDrawerToggle}
                  className={classes.navIconHide}
                >
                  <Menu />
                </IconButton>
                <Typography variant="h6" color="inherit" noWrap>
                  E-Reg
                </Typography>
              </Toolbar>
            </AppBar> */}
            <Hidden mdUp>
              <Drawer
                variant="temporary"
                open={mobileOpen}
                onClose={this.handleDrawerToggle}
                classes={{
                  paper: classes.drawerPaper,
                }}
                ModalProps={{
                  keepMounted: true, // Better open performance on mobile.
                }}
              >
                {drawer}
              </Drawer>
            </Hidden>
            <Hidden smDown implementation="css">
              <Drawer
                variant="permanent"
                open
                classes={{
                  paper: classes.drawerPaper,
                }}
              >
                {drawer}
              </Drawer>
            </Hidden>
            <main className={classes.content}>
              {/* <div className={classes.toolbar} /> */}
              {children}
            </main>
          </div>
        </div>
      </ThemeProvider>
    );
  }
}

export default compose(withRouter, withStyles(styles))(Layout);
