import React from "react";
import Button from "@material-ui/core/Button";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import Divider from "@material-ui/core/Divider";

import auth from "../../../helpers/Auth";

import axios from "axios";

export default function ItemMenu({ fetch, id }) {
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleDelete = () => {
    const userData = auth.currentUser();
    const email = userData.emails[0];
    axios
      .delete(`${process.env.REACT_APP_API}DeleteContainer?id=${id}`, {
        headers: {
          Authorization: "token",
          AccessEmail: email,
        },
      })
      .then((response) => {
        fetch();
      })
      .catch((error) => {
        console.log(error.message);
      });
  };

  return (
    <div>
      <Button
        aria-controls="simple-menu"
        aria-haspopup="true"
        onClick={handleClick}
      >
        ...
      </Button>
      <Menu
        id="simple-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <MenuItem className="erc-menu-item">More Info</MenuItem>
        <Divider />
        <MenuItem className="erc-menu-item erc-red" onClick={handleDelete}>
          Delete
        </MenuItem>
      </Menu>
    </div>
  );
}
